import React, { Component, Fragment } from 'react';

export default class Log extends Component{
    constructor(props){
        super(props);
        this.state = {
            logList: [
                {
                    version: 'v0.5.1',
                    time: '2018/12/22',
                    tag: 'bug&fix',
                    description: `
                        <div>1.</div>
                        `,
                    isCompatible: true
                },
                {
                    version: 'v0.4.1',
                    time: '2018/12/12',
                    tag: 'add&fix',
                    description: `
                        <div>1.</div>
                        <div>2.</div>
                        `,
                    isCompatible: true
                },
                {
                    version: 'v0.3.1',
                    time: '2018/11/22',
                    tag: 'add',
                    description: '<a href="/slider"></a>',
                    isCompatible: true
                },
                {
                    version: 'v0.2.1',
                    time: '2018/10/12',
                    tag: 'add',
                    description: '<a href="/plugin"></a>',
                    isCompatible: true
                },
                {
                    version: 'v0.1.1',
                    time: '2018/09/08',
                    tag: 'add',
                    description: '<a href="/form"></a>',
                    isCompatible: true
                },
                {
                    version: 'v0.0.1',
                    time: '2018/08/10',
                    tag: 'init',
                    description: '1.vueui<a href="https://github.com/xumengzi/xui-react" target="_blank">github</a>'
                },
            ]
        }
    }

    render(){
        function changeToHtml(content){
            return{
                __html: content
            }
        }
        return(
            <Fragment>
                <h1></h1>
                <h6>: <span className="version">0.0.1</span></h6>
                <blockquote>
                    <p>:  bug,</p>
                </blockquote>

                {
                    this.state.logList.map(item=>{
                        return(
                            <Fragment key={item.version.toString() + Math.random().toFixed(3)}>
                                <h3>{item.version}  <span style={{fontSize: 16}}>{item.time}</span></h3>
                                <h5>{item.tag}</h5>
                                <p className="log" dangerouslySetInnerHTML={changeToHtml(item.description)}></p>
                                {
                                    item.isCompatible && <blockquote>
                                                            <p></p>
                                                        </blockquote>
                                }
                            </Fragment>
                        )
                    })
                }
                
            </Fragment>
        )
    }
}


